.shopable-image-text p{margin:0 !important}.shopable-image-text .container{display:flex;justify-content:space-between;max-width:80%;margin:0 auto}@media screen and (max-width: 768px){.shopable-image-text .container{flex-direction:column;max-width:100%;gap:2rem}}.shopable-image-text .container .shopable-image-text__image__container{width:50%;height:100%;position:relative;overflow:hidden}.shopable-image-text .container .shopable-image-text__image__container:hover .shopable-image-text__shop{visibility:visible;opacity:1}@media screen and (max-width: 768px){.shopable-image-text .container .shopable-image-text__image__container{width:100%}}.shopable-image-text .container .shopable-image-text__image__container .shopable-image-text__image__img{width:100%;height:100%;object-fit:cover;display:block}.shopable-image-text .container .shopable-image-text__image__container .shopable-image-text__shop{visibility:hidden;opacity:0;padding:2rem;position:absolute;bottom:0;right:0;z-index:1;transition:all .3s}@media screen and (max-width: 768px){.shopable-image-text .container .shopable-image-text__image__container .shopable-image-text__shop{visibility:visible;opacity:1}}.shopable-image-text .container .shopable-image-text__image__container .shopable-image-text__shop button{color:var(--shop-color) !important}.shopable-image-text .container .shopable-image-text__image__container .shopable-image-text__shop button:hover{color:var(--silver) !important}.shopable-image-text .container .shopable-image-text__content{width:40%;height:auto}@media screen and (max-width: 768px){.shopable-image-text .container .shopable-image-text__content{width:100%}}.shopable-image-text .container .shopable-image-text__content .shopable-image-text__description{text-align:justify}